.pricing {
  margin-top: 60px;
  text-align: center;
}

.pricing__item {
  box-sizing: border-box;
  background-color: white;
  padding: 20px 40px;
  border-radius: 6px;
  width: 380px;
  max-width: 100%;
  margin: 40px auto 60px;
  border: 1px solid #ddd;
  text-align: left;
  transition: box-shadow 0.2s;
}
.pricing__item:hover {
  background-color: #fefefe;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.pricing__amount {
  font-size: 24px;
  padding-bottom: 15px;
  margin-bottom: 15px;
  border-bottom: 4px solid #ddd;
  color: #999;
}
.pricing__amount span {
  font-size: 36px;
  color: #333;
}

.pricing__details {
  list-style: none;
  padding: 0;
}
.pricing__details li {
  line-height: 2;
  background: transparent url(./assets/ok.svg) left 4px no-repeat;
  background-size: 24px 24px;
  padding-left: 32px;
}

.pricing__button {
  appearance: none;
  -webkit-appearance: none;
  background-color: #f24e3d;
  color: white;
  border: 0;
  border-radius: 8px;
  padding: 10px;
  display: block;
  width: 100%;
  transition: opacity 0.2s;
  opacity: 0.8;
}
.pricing__button:hover {
  opacity: 1;
}

.question {
  line-height: 1.6;
  margin-bottom: 60px;
}
.question h3 {
  margin-bottom: -8px;
}